/**
 *	Start window function
 */
@keyframes window_start {
	0% {
 		opacity: 0;
		transform: 	scale(.85);
 	}
 	100% {
 		opacity: 1;
		transform: 	scale(1);
 	}
}


/**
 *	End window function
 */
@keyframes window_end {
	0% {
 		opacity: 1;
		transform: 	scale(1);
 	}
 	100% {
 		opacity: 0;
		transform: 	scale(.85);
 	}
}


/**
 *	Minimize function
 */
@keyframes window_minimize {
	0% {
		opacity: 1;
		transform: 	rotate(0)
					translate(0, 0)
					scale(1);
	}
	100% {
		opacity: 0;
		transform: 	rotate(2deg)
					translate(0, 100px)
					scale(.7);
	}
}
